@import "color";



.main {
  position: relative;
  &__item {
    position: absolute;
    font-size: 10rem;
    color: #e9e9e9;
    font-weight: 400;
  }
  &__subtitle {
    position: absolute;
    display: flex;
    max-width: 615px;
    width: 100%;
    height: 100px;
    border: 3px solid #000000;
    top: 90px;
    left: 30px;
    z-index: 5;

    .subtitle_item-left {
      font-size: 3.4rem;
      font-weight: 700;
      height: 100%;
      width: 250px;
      text-align: center;
    }
    .subtitle_item-right {
      font-size: 5.5rem;
      font-weight: 700;
      background-color: rgba(0, 0, 0, 0.3);
      width: 360px;
      color: #FFFFFF;
      height: 100%;
      text-align: center;
    }
  }
  &__wrap {
    display: block;
    position: relative;
    right: 0;
    box-shadow: 0px 0px 2px 4px rgba(0,0,0,.2);
    width: 915px;
    height: 490px;
    margin-top: 50px;
    margin-right: 40px;
    margin-left: auto;
    &:before {
      content: '';
      position: absolute;
      background-image: url("../img/bg-main.png");
      background-size: cover;
      background-repeat: no-repeat;
      width: 800px;
      height: 365px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
}